import {
  USER_DETAILS_FAIL,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_REGISTER_FAIL,
  USER_REGISTER_REQUEST,
  USER_REGISTER_SUCCESS,
  USER_SIGNIN_FAIL,
  USER_SIGNIN_REQUEST,
  USER_SIGNIN_SUCCESS,
  USER_SIGNOUT,
  USER_UPDATE_PROFILE_FAIL,
  USER_UPDATE_PROFILE_REQUEST,
  USER_UPDATE_PROFILE_RESET,
  USER_UPDATE_PROFILE_SUCCESS,
  USER_LIST_REQUEST,
  USER_LIST_SUCCESS,
  USER_LIST_FAIL,
  USER_DELETE_RESET,
  USER_DELETE_FAIL,
  USER_DELETE_SUCCESS,
  USER_DELETE_REQUEST,
  USER_UPDATE_REQUEST,
  USER_UPDATE_SUCCESS,
  USER_UPDATE_FAIL,
  USER_UPDATE_RESET,
  USER_DETAILS_RESET,
  USER_TOPSELLERS_LIST_REQUEST,
  USER_TOPSELLERS_LIST_SUCCESS,
  USER_TOPSELLERS_LIST_FAIL,
  USER_BECOMESELLER_SUCCESS,
  USER_BECOMESELLER_FAIL,
  USER_BECOMESELLER_REQUEST,
  LIST_SELLER_REQUEST,
  LIST_SELLER_SUCCESS,
  LIST_SELLER_FAIL
} from "../constants/userConstants"

export const userSignInReducer = (state = {}, { type, payload }) => {
  switch (type) {
    case USER_SIGNIN_REQUEST:
      return {
        loading: true
      }
    case USER_SIGNIN_SUCCESS:
      return {
        loading: false,
        userInfo: payload
      }
    case USER_SIGNIN_FAIL:
      return {
        loading: false,
        error: payload
      }
    case USER_SIGNOUT:
      return {}

    default:
      return state
  }
}

export const userRegisterReducer = (state = {}, { type, payload }) => {
  switch (type) {
    case USER_REGISTER_REQUEST:
      return {
        loading: true
      }
    case USER_REGISTER_SUCCESS:
      return {
        loading: true,
        userInfo: payload
      }
    case USER_REGISTER_FAIL:
      return {
        loading: false,
        error: payload
      }
    default:
      return state;
  }
}
export const userDetailsReducer = (state = { loading: true }, { type, payload }) => {
  switch (type) {
    case USER_DETAILS_REQUEST:
      return {
        loading: true
      }
    case USER_DETAILS_SUCCESS:
      return {
        loading: false,
        user: payload
      }
    case USER_DETAILS_FAIL:
      return {
        loading: false,
        error: payload
      }
    case USER_DETAILS_RESET:
      return { loading: true }
    default:
      return state;
  }
}

export const userUpdateProfileReducer = (state = {}, { type, payload }) => {
  switch (type) {
    case USER_UPDATE_PROFILE_REQUEST:
      return { loading: true };
    case USER_UPDATE_PROFILE_SUCCESS:
      return { loading: false, success: true };
    case USER_UPDATE_PROFILE_FAIL:
      return { loading: false, error: payload };
    case USER_UPDATE_PROFILE_RESET:
      return {};
    default:
      return state;
  }
};

export const userListReducer = (state = { loading: true, users: [] }, { type, payload }) => {
  switch (type) {
    case USER_LIST_REQUEST:
      return { loading: true }
    case USER_LIST_SUCCESS:
      return { loading: false, users: payload }
    case USER_LIST_FAIL:
      return { loading: false, error: payload }
    default:
      return state
  }
}
export const userDeleteReducer = (state = { user: {} }, { type, payload }) => {
  switch (type) {
    case USER_DELETE_REQUEST:
      return { loading: true }
    case USER_DELETE_SUCCESS:
      return { loading: false, user: payload, success: true }
    case USER_DELETE_FAIL:
      return { loading: false, error: payload }
    case USER_DELETE_RESET:
      return {}
    default:
      return state
  }
}
